@import 'normalize.scss';
@import url('https://fonts.googleapis.com/css2?family=Onest:wght@100..900&display=swap');

:root {
  --font-family: 'Onest', sans-serif;
  --regular-font: 400;
  --medium-font: 500;
  --semi-bold-font: 600;

  --font-size-12: 12px;
  --font-size-14: 14px;
  --font-size-16: 16px;
  --font-size-h2: 24px;
  --font-size-h4: 18px;

  --text-color-primary: #171a1c;
  --text-color-secondary: #95a0a9;
  --bg-surface: #f6f7f8;
  --card-gradient-to: #e3e5e8;
  --button-primary-bg: #1ccb01;

  --white-color: white;
  --shadow-color: rgba(0, 0, 0, 0.407);

  --space-3xs: 2px;
  --space-xs: 8px;
  --space-s: 12px;
  --space-m: 16px;
  --space-x: 20px;
  --space-xl: 24px;
  --space-2xl: 32px;

  --radius-none: 0px;

  --radius-s: 16px;
  --radius-m: 24px;
  --radius-l: 32px;
}

$screen-mobile: 360px;
$screen-laptop: 1208px;

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.body {
  background-color: var(--bg-surface);
}

.page {
  font-family: var(--font-family);
  text-align: center;

  min-width: $screen-mobile;
  max-width: $screen-laptop;

  margin: 0 auto;

  &-logo {
    height: 92px;
    margin-bottom: var(--space-xl);
    color: var(--text-color-primary);
    align-self: center;
  }

  &-title {
    margin-bottom: var(--space-xl);
    font-size: var(--font-size-h2);
    font-weight: var(--semi-bold-font);
  }
}

.header {
  &-small {
    display: none;

    @media (max-width: $screen-laptop) {
      display: block;
    }
  }

  &-big {
    display: none;

    padding-top: calc(var(--space-2xl) + var(--space-s));

    background-color: var(--white-color);
    border-radius: var(--radius-none) var(--radius-none) var(--radius-l)
      var(--radius-l);
    padding-bottom: var(--space-xl);
    margin-bottom: var(--space-xs);

    @media (min-width: $screen-laptop) {
      display: block;
    }
  }
}

.card-field {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0 auto;
  gap: var(--space-xs);

  @media (min-width: $screen-laptop) {
    flex-direction: row;

    justify-content: space-around;
  }
}

.card {
  max-width: 600px;
  box-sizing: border-box;
  text-align: center;
  margin: 0 auto;
  padding: var(--space-m);
  margin-bottom: var(--space-xs);
  border-radius: var(--radius-l);
  background-color: var(--white-color);

  display: flex;
  flex-direction: column;
  justify-content: center;

  @media (min-width: $screen-laptop) {
    flex: 1 1 1;
    margin: 0;
  }

  &--first {
    border-radius: var(--radius-none) var(--radius-none) var(--radius-l)
      var(--radius-l);

    @media (min-width: $screen-laptop) {
      border-radius: var(--radius-l);
    }
  }

  &__content {
    text-align: start;
    border-radius: var(--radius-m);
    padding: var(--space-xl) var(--space-x) 0 var(--space-xl);
    background: linear-gradient(
      var(--bg-surface) 85%,
      var(--bg-surface) 85%,
      var(--card-gradient-to) 100%
    );
    box-shadow: 0px 2px 5px 0px var(--shadow-color);

    @media (min-width: $screen-laptop) {
      height: 100%;
    }
  }

  &__title {
    font-size: var(--font-size-h4);
    font-weight: var(--medium-font);
    margin-bottom: var(--space-xs);
    color: var(--text-color-primary);
  }

  &__text {
    display: block;
    font-size: var(--font-size-14);
    font-weight: var(--regular-font);
    color: var(--text-color-secondary);

    &--mb-3xs {
      margin-bottom: var(--space-3xs);
    }

    &--mb-m {
      margin-bottom: var(--space-m);
    }

    &--mb-2xl {
      margin-bottom: var(--space-2xl);
    }
  }

  &__img {
    width: 100%;
  }

  &__download-button {
    width: 100%;
    margin-top: var(--space-m);
    color: var(--white-color);
    border: none;
    min-height: 48px;

    border-radius: var(--radius-s);
    background: var(--button-primary-bg);

    &:not(:disabled):active {
      box-shadow: inset 0px 0px 20px 1px var(--shadow-color);
    }

    &:disabled {
      background: var(--text-color-secondary);
    }
  }
}

.info {
  display: flex;
  text-align: start;
  align-items: center;
  border-radius: var(--radius-m);
  padding: var(--space-m);
  background-color: var(--bg-surface);
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.407);
  margin-bottom: var(--space-s);

  &__icon {
    width: 56px;
    height: 56px;
    margin-right: var(--space-m);
  }

  &__title {
    color: var(--text-color-primary);
    font-size: var(--font-size-14);
    font-weight: var(--regular-font);
    line-height: calc(var(--font-size-14) * 1.3);
    margin-bottom: var(--space-3xs);
  }

  &__text {
    color: var(--text-color-secondary);
    font-size: var(--font-size-12);
    font-weight: var(--regular-font);

    &-group {
      line-height: calc(var(--font-size-12) * 1.3);
    }
  }
}
